
import { defineComponent } from 'vue'
import BaseInput from "@/components/FormElements/BaseTextInput.vue"
import HisKeyboard from "@/components/Keyboard/HisKeyboard.vue"
import handleVirtualInput from "@/components/Keyboard/KbHandler"
import { NUMBERS_ONLY } from "@/components/Keyboard/HisKbConfigurations"
import ViewPort from "@/components/DataViews/ViewPort.vue"
import FieldMixinVue from './FieldMixin.vue'
import {
    IonItem,
    IonList,
    IonLabel,
    IonRadio,
    IonRadioGroup
} from "@ionic/vue"
import dayjs from "dayjs"
import HisDate, { STANDARD_DATE_FORMAT } from "@/utils/Date"

export default defineComponent({
    components: { 
        BaseInput, 
        HisKeyboard, 
        ViewPort,
        IonItem,
        IonList,
        IonLabel,
        IonRadio,
        IonRadioGroup
    },
    mixins: [FieldMixinVue],
    data: ()=> ({
        value: '',
        selectedTimeUnit: '' as '' 
            | 'Days'
            | 'Hours' 
            | 'Weeks' 
            | 'Months' 
            | 'Years',
        timeUnits: [] as string[],
        keyboard: NUMBERS_ONLY,
    }),
    mounted() {
        this.timeUnits = [
           'Hours',
           'Days',
           'Weeks',
           'Months',
           'Years'
        ]
        if (Array.isArray(this.config.excludeUnits)) {
            this.timeUnits = this.timeUnits.filter(
                unit => !this.config.excludeUnits.includes(unit)
            )
        }
        this.$emit('onFieldActivated', this)
    },
    activated() {
        this.$emit('onFieldActivated', this)
    },
    methods: {
        clearValue() {
            this.value = ''
            this.selectedTimeUnit = ''
        },
        onKbValue(text: any) { 
            this.value = text
        },
        keypress(text: any){
            this.value = handleVirtualInput(text, this.value)
        },
        emitValue(value: string | number, unit: string){
            if (!(value && unit)) {
                this.$emit('onValue', null)
            } else {
                const v = `${value} ${unit}`
                const date = dayjs(HisDate.sessionDate()).subtract(parseInt(`${value}`), unit as any)
                this.$emit('onValue', !value ? null : { 
                    label: v,
                    value: v,
                    other: {
                        value,
                        timeUnit: unit,
                        monthsAgoInt: dayjs(HisDate.sessionDate()).diff(date, 'month'),
                        daysAgoInt: dayjs(HisDate.sessionDate()).diff(date, 'day'),
                        date: date.format(STANDARD_DATE_FORMAT)
                    }
                })
            }
        }
    },
    watch: {
        value(value: string) {
            this.emitValue(value, this.selectedTimeUnit)
        },
        selectedTimeUnit(unit: string) {
            this.emitValue(this.value, unit)
        }
    }
})
